canvas {
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
}

:root {
  --dark-bg: #2b7c3df2;
  --spacing: 350px;
  font-family: brandon-grotesque, sans-serif;
  font-style: normal;
  font-weight: 400;
}

main {
  color: #fff;
  grid-template-columns: repeat(12, 1fr);
  width: 100%;
  margin: 0 auto;
  padding: 120px 0;
  display: grid;
  position: absolute;
}

h1, h2, h3, blockquote {
  font-family: elevon, sans-serif;
  font-style: normal;
  font-weight: 700;
}

header {
  background: var(--dark-bg);
  margin-top: 50px;
  margin-bottom: var(--spacing);
  border-radius: 20px;
  grid-column: 2 / span 5;
  align-items: center;
  padding: 2rem;
  font-size: 1.5rem;
}

section {
  background: var(--dark-bg);
  margin-bottom: var(--spacing);
  border-radius: 20px;
  grid-column: 2 / 8;
  padding: 1rem;
  font-size: 1rem;
  line-height: 1.5;
}

blockquote {
  border-left: 4px solid var(--dark-bg);
  margin: 0;
  margin-bottom: var(--spacing);
  background-color: #ffffff4d;
  border-radius: 10px;
  grid-column: 2 / span 9;
  padding-left: 1rem;
  font-size: 2rem;
  line-height: 1.5;
}

blockquote p {
  color: #000;
  font-size: 2.5rem;
  line-height: 1.5;
}

.left {
  grid-column: 7 / 12;
}

.leftm {
  grid-column: 7 / 12;
  align-items: center;
  display: flex;
}

.manifesto-content {
  align-items: center;
  display: flex;
}

.manifesto-image {
  width: 200px;
  height: auto;
  margin-right: 20px;
}

.manifesto-text {
  font-size: 1rem;
}
